'use client';

import { useEffect, useState } from 'react';
import { useParams, useRouter } from 'next/navigation';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { supabase } from '@/lib/supabase';

export default function EditStorePage() {
  const { id } = useParams();
  const router = useRouter();
  const [form, setForm] = useState<any>(null);

  useEffect(() => {
    const fetchStore = async () => {
      const { data } = await supabase.from('store_admin').select('*').eq('id', id).single();
      if (data) setForm(data);
    };
    fetchStore();
  }, [id]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleUpdate = async () => {
    await supabase.from('store_admin').update(form).eq('id', id);
    router.push('/admin/dashboard/manage-store');
  };

  if (!form) return <p>Loading...</p>;

  return (
    <div className="max-w-md mx-auto space-y-4">
      <h2 className="text-xl font-semibold mb-4">Edit Store</h2>

      <Input name="name" value={form.name} onChange={handleChange} />
      <Input name="city" value={form.city} onChange={handleChange} />
      <select name="type" value={form.type} onChange={handleChange} className="w-full border p-2 rounded">
        <option value="hub">Hub</option>
        <option value="garage">Garage</option>
      </select>
      <Input name="alias" value={form.alias} onChange={handleChange} />
      <Input name="password" type="password" value={form.password} onChange={handleChange} />

      <Button onClick={handleUpdate} className="w-full">Update</Button>
    </div>
  );
}
